import React from 'react'
import styled from 'styled-components'

const StyledProjectCard = styled.div`
    width: 280px;
    background-color: #734967;
    height: fit-content;
    box-shadow: 1px 5px 5px 5px #FFB7CB;
    margin: 10px;
    position: relative;
    font-size: 16px;
    border-radius: 5px;

    @media (max-width: 700px) {
        width: 250px;
        font-size: 13.5px;
    }
`

const CardImage = styled.div`
display: flex;
justify-content: center;
cursor: pointer;

img {
    width: 270px;
    height: 200px;
    margin: 5px;

}

@media (max-width:700px) {
    img {
        width: 240px;
        height: 160px;
    }
}
`

const CardDetails = styled.div`
height: 280px;
cursor: default;

  hr {
      width: 80%;
  }
`

const CardTitle = styled.div`
width: 100%;
text-align: center;
padding: 2px;
color: #FF859E;

h4 {
    margin: 0px;
}
`

const CardDescription = styled.div`
text-align: center;
padding: 5px;
`
const CardTechs = styled.div`
text-align: center;
`

const CardLink = styled.div`
text-align: center;

  a {
      text-decoration: none;
      color: #FF859E;

      &:hover {
          color: #D70170;
      }
  }
`


const ProjectCard = ({image, description, title, cardlink, techs, isENG}) => {
    return (
        <StyledProjectCard>
            <CardImage>
                <img src={image} alt="project preview"/>
            </CardImage>
                <CardDetails>
                    <CardTitle><h4>{title}</h4></CardTitle>
                    <hr></hr>
                    <CardDescription>{description}</CardDescription>
                    <hr></hr>
                    <CardTechs>{techs}</CardTechs>
                    <hr></hr>
                    <CardLink><a href={cardlink}>{isENG ? "View here" : "Zobacz tutaj"}</a></CardLink>
                </CardDetails>
                
        </StyledProjectCard>
    )
}

export default ProjectCard
