import React from 'react'
import styled from 'styled-components'
import wojtek from './wojtek.jpg'
import {opacityAnimation} from '../animations'
import Skills from './Skills'
import {aboutENG, aboutPL} from './aboutData'

const StyledAbout = styled.div`
display: flex;
flex-direction: column;
align-items: center;
background-color: #3D2A35;
animation: ${opacityAnimation} 1s ease-in-out;
padding-bottom: 18%;
`
const AboutContent = styled.div`
display: flex;
height: fit-content;
padding-top: 50px;


@media (max-width: 700px) {
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding-top: 50px;
    padding-bottom: 20px;
}
`
const AboutTitle = styled.div`

color: #D70170;
font-size: 1.5rem;
font-family: 'Staatliches', cursive;

@media (max-width: 700px) {
    font-size: 1.2rem;
} 

`
const AboutInfo = styled.div`
background-color: #3D2A35;
color: #FFB7CB;
width: 60%;
text-align: left;
@media (max-width: 700px) {
    width: 90%;
}
`

const AboutText = styled.div`
font-size: 1.1rem;

@media (max-width: 700px) {
    font-size: 1rem;
}

`

const AboutSkills = styled.div`

`

const AboutImage = styled.div`
display: flex;
justify-content: flex-end;
margin-right: 2%;
width: 30%;
    img {
        max-height: 280px;
        border: 1px solid #D70170;
        border-radius: 10px;
        box-shadow: -1px -1px 5px 5px #FF859E;
    }
    @media (max-width: 1024px) {
        img {
            max-height: 230px;
        }
    }
    @media (max-width: 700px) {
        width: 90%;
        justify-content: center;
     img {
         max-height: 140px;
         margin-bottom: 10%;
     }   
    }
`
const About = ({isENG}) => {
    return (
        <StyledAbout>
            <AboutContent>
                <AboutImage>
                    <img src={wojtek} alt="my faces"/>
                </AboutImage>
                <AboutInfo>
                <AboutTitle>{isENG ? "About me" : "O mnie"}</AboutTitle>
                {isENG ? (
                    <AboutText>{aboutENG}
                    </AboutText>
                ) : (<AboutText>{aboutPL}
                    </AboutText>)}
                    <AboutSkills>
                        <Skills isENG={isENG}/>
                    </AboutSkills>
                </AboutInfo>
                
            </AboutContent>
        </StyledAbout>
    )
}

export default About
