import React from 'react'
import styled from 'styled-components'

const StyledSkills = styled.div`
width: 100%;
margin-top: 30px;
`

const SkillTitle = styled.div`
color: #D70170;
font-size: 1.5rem;
font-family: 'Staatliches', cursive;

@media (max-width: 700px) {
    font-size: 1.2rem;
} 
`

const SkillsContent = styled.div`
display: flex;
flex-wrap: wrap;

@media (max-width: 700px) {
    justify-content: center;
}
`

const SkillCell = styled.div`
padding: 0px 10px 10px 0px;
width: 100px;

.checked {
    color: #D70170;
}
h4 {
    margin: 12px 0px;
}

@media (max-width: 700px) {
    font-size: 0.9rem;
}
`

const Skills = ({isENG}) => {
    return (
        <StyledSkills>
            <SkillTitle>
               {isENG ? "My skills" : "Moje umiejętności"}
            </SkillTitle>
            <SkillsContent>
            <SkillCell>
                <h4>HTML</h4>
                <span className="fa fa-star checked"></span>
                <span className="fa fa-star checked"></span>
                <span className="fa fa-star checked"></span>
                <span className="fa fa-star checked"></span>
                <span className="fa fa-star checked"></span>
            </SkillCell>
            <SkillCell>
                <h4>CSS</h4>
                <span className="fa fa-star checked"></span>
                <span className="fa fa-star checked"></span>
                <span className="fa fa-star checked"></span>
                <span className="fa fa-star checked"></span>
                <span className="fa fa-star checked"></span>
            </SkillCell>
            <SkillCell>
                <h4>JavaScript</h4>
                <span className="fa fa-star checked"></span>
                <span className="fa fa-star checked"></span>
                <span className="fa fa-star checked"></span>
                <span className="fa fa-star"></span>
                <span className="fa fa-star"></span>
            </SkillCell>
            <SkillCell>
                <h4>jQuery</h4>
                <span className="fa fa-star checked"></span>
                <span className="fa fa-star checked"></span>
                <span className="fa fa-star checked"></span>
                <span className="fa fa-star "></span>
                <span className="fa fa-star "></span>
            </SkillCell>
            <SkillCell>
                <h4>React Native</h4>
                <span className="fa fa-star checked"></span>
                <span className="fa fa-star checked"></span>
                <span className="fa fa-star checked"></span>
                <span className="fa fa-star "></span>
                <span className="fa fa-star "></span>
            </SkillCell>
            <SkillCell>
                <h4>Rest API</h4>
                <span className="fa fa-star checked"></span>
                <span className="fa fa-star checked"></span>
                <span className="fa fa-star checked"></span>
                <span className="fa fa-star "></span>
                <span className="fa fa-star "></span>
            </SkillCell>
            <SkillCell>
                <h4>Python</h4>
                <span className="fa fa-star checked"></span>
                <span className="fa fa-star checked"></span>
                <span className="fa fa-star "></span>
                <span className="fa fa-star "></span>
                <span className="fa fa-star "></span>
            </SkillCell>
            <SkillCell>
                <h4>Django</h4>
                <span className="fa fa-star checked"></span>
                <span className="fa fa-star checked"></span>
                <span className="fa fa-star "></span>
                <span className="fa fa-star "></span>
                <span className="fa fa-star "></span>
            </SkillCell>
            <SkillCell>
                <h4>GitHub</h4>
                <span className="fa fa-star checked"></span>
                <span className="fa fa-star checked"></span>
                <span className="fa fa-star checked"></span>
                <span className="fa fa-star checked"></span>
                <span className="fa fa-star "></span>
            </SkillCell>
            <SkillCell>
                <h4>RWD</h4>
                <span className="fa fa-star checked"></span>
                <span className="fa fa-star checked"></span>
                <span className="fa fa-star checked"></span>
                <span className="fa fa-star checked"></span>
                <span className="fa fa-star checked"></span>
            </SkillCell>
            </SkillsContent>
        </StyledSkills>
    )
}

export default Skills
