import React from 'react'
import styled from 'styled-components'
import poland from './poland.png'
import uk from './uk.png'
import { navbarAnimation} from '../animations'
import logo from './logo.png'

const StyledNavbar = styled.div`
color: #FFB7CB;
background-color: black;
width: 100%;
display: flex;
position: sticky;
left: 0;
top: 0;
z-index: 2;
height: 70px;
animation: 1s ease-out 0s ${navbarAnimation};
`
const NavbarLeft = styled.div`
width: 30%;
display: flex;
align-items: center;
padding: 5px;
color: #D70170;

img {
    height: 60px;
}

@media (max-width: 700px) { 
    img {
        height: 40px;
    }
}
`

const NavbarRight = styled.div`
display: flex;
align-items: center;
justify-content: flex-end;
width: 70%;

img {
    height: 20px;
    width: 20px;
    padding: 5px 10px;
    cursor: pointer;
}

}
`

const NavMenu = styled.div`
display: flex;

@media (max-width: 700px) {
position: absolute;
top: 70px;
left: 0;
width: 100%;
align-items: center;
justify-content: center;
background-color: black;
display: ${({isNavbar}) => isNavbar ? 'flex' : 'none'}
}
`

const Navlink = styled.div`
padding: 5px;
flex-wrap: no-wrap;
font-family: 'Questrial', sans-serif;
font-size: 1.1rem;
display: flex;
align-items: center;

&:hover {
    color: #D70170;
    cursor: pointer;
}

@media (max-width: 700px) { 
    justify-content: center;
    width: 100px;
    font-size: 1rem;
}
`
const MenuButton = styled.div`
display: none;
@media (max-width: 700px) {
    display: block;
    padding-left: 20px;
  }
`
const StyledBurger = styled.div`
width: 20px;
height: 20px;
display: flex;
justify-content: space-around;
flex-flow: column nowrap;
z-index: 2;
cursor: pointer;
transform-origin: 1px;
transition: all 0.3s;
margin-right: 20px;
padding: 2px;

  div {
      width: 20px;
      height: 3px;
      background-color: #FFB7CB;
      border-radius: 10px;
      transform-origin: 1px;
      transition: all 0.3s;
    &:nth-child(1) {
        transform: ${({open}) => open ? 'rotate(45deg)' : 'rotate(0)'};
    }

    &:nth-child(2) {
      transform: ${({open}) => open ? 'translateX(100%)' : 'translateX(0)'};
      opacity: ${({open}) => open ? 0 : 1};
    }

    &:nth-child(3) {
      transform: ${({open}) => open ? 'rotate(-45deg)' : 'rotate(0)'};
  }
      }
      
  }


  `;
const Navbar = ({open, setOpen, isNavbar, setIsNavbar, setIsHome, setIsAbout, setIsProjects, setIsContact, isENG, setIsENG}) => {
    return (
        <StyledNavbar>  

            <NavbarLeft>
                <img src={logo} alt="logo"/>
            </NavbarLeft>
            <NavbarRight> 
                <NavMenu isNavbar={isNavbar}>
                <Navlink onClick={() => {
                    setIsHome(true)
                    setIsAbout(false)
                    setIsProjects(false)
                    setIsContact(false)}}>
                        {isENG ? "Home" : "Główna"}
                </Navlink>
                <Navlink onClick={() => {
                    setIsHome(false)
                    setIsAbout(true)
                    setIsProjects(false)
                    setIsContact(false)}}>
                        {isENG ? "About" : "O mnie"}
                </Navlink>
                <Navlink onClick={() => {
                    setIsHome(false)
                    setIsAbout(false)
                    setIsProjects(true)
                    setIsContact(false)}}>
                        {isENG ? "Projects" : "Projekty"}
                </Navlink>
                <Navlink onClick={() => {
                    setIsHome(false)
                    setIsAbout(false)
                    setIsProjects(false)
                    setIsContact(true)}}>
                        {isENG ? "Contact" : "Kontakt"}
                </Navlink>
                </NavMenu>
                {isENG ? (
                    <img src={poland} alt="polish" onClick={() => {
                        setIsENG(false)
                        }}/>
                ) :(<img src={uk} alt="english" onClick={() => {
                        setIsENG(true)
                        }}/>
                )}
                
                
                <MenuButton>
                    <StyledBurger open={open} onClick={() => {
                        setOpen(!open)
                        setIsNavbar(!isNavbar)
                        }}>
                        <div />
                        <div />
                        <div />
                    </StyledBurger>
                </MenuButton>
            </NavbarRight>
        </StyledNavbar>
    )
}

export default Navbar
