import React from 'react'
import emailjs from 'emailjs-com'
import styled from 'styled-components'
import Socialls from '../Socials/Socials'
import {opacityAnimation} from '../animations'
import location from './location1.png'
import phone from './phone.png'
import email from './email.png'

const StyledContact = styled.div`
display: flex;
flex-direction: column;
align-items: center;
background-color: #3D2A35;
height: fit-content;
padding-top: 20px;
padding-bottom: 10%;
animation: ${opacityAnimation} 1s ease-in-out;


`
const ContactTitle = styled.div`
font-family: 'Staatliches', cursive;
width: 100%;
text-align: center;
h2 {
    color: #D70170;
    font-size: 1.5rem;
    font-weight: 100;
}

h4 {
    color: #FF859E;
    font-family: 'Questrial', sans-serif; 
    font-size: 17px;
    font-weight: 100;
    padding-bottom: 10px;
}

@media (max-width: 700px) {
    h2 {
        font-size: 1.2rem;
    }

    h4 {
        font-size: 14px;
    }
}
`

const ContactContent = styled.div`
display: flex;
background-color: #734967;
box-shadow: -1px -1px 5px 5px #FF859E;
font-family: 'Questrial', sans-serif;
color: #FFB7CB;
align-items: center;
height: fit-content;
border-radius: 5px;

@media (max-width: 700px) {
    flex-direction: column;
    width: 80%;
    
}
`

const ContactForm = styled.div`
display: flex;
flex-direction: column;
justify-content: center;
width: 50%;   
margin: 30px;
padding: 30px;
    div {
        padding: 5px;
        display: flex;
        flex-direction: column; 
        align-items: center;
        justify-content: center;
    }

    input {
        background-color: #3D2A35;
        color: #FFB7CB;
        width: 300px;
        margin: 5px;
        border-radius: 5px;
        height: 22px;
        font-family: 'Questrial', sans-serif;
        font-size: 16px;
        outline: none;
        border: 1px solid #FF859E;

        &:focus {
            border: 1px solid #D70170;
        }

    }

    textarea {
        background-color: #3D2A35;
        color: #FFB7CB;
        height: 200px;
        resize: none;   
        width: 300px;
        border-radius: 5px;
        font-family: 'Questrial', sans-serif;
        margin-top: 10px;
        font-size: 16px;
        border: 1px solid #FF859E;
        outline: none;
        &:focus {
            border: 1px solid #D70170;
        }
    }

    input[type=submit] {
        padding: 5px;
        margin-top: 15px;
        background-color: #D70170;
        font-family: 'Questrial', sans-serif;
        cursor: pointer;
        font-size: 1.05rem;
        border-radius: 10px;
        height: 30px;
        font-weight: 600;
        color: #FF859E;
        border: none;

        &:hover {
            background-color: #FF859E; 
            color: #D70170;
        }
    }

@media (max-width: 700px) {
    margin: 10px;
    padding: 5px;
    width: 90%;

    div {
        padding: 2px;
    }

    input {
        width: 220px;
        margin: 3px;
        height: 18px;
        font-size: 12px;
    }

    textarea {
        height: 140px;  
        width: 220px;
        margin-top: 3px;
        font-size: 12px;
    }

    input[type=submit] {
        padding: 3px;
        margin-top: 5px;
        font-size: 12px;
        height: 24px;
}
`

const ContactInfo = styled.div`
width: 50%;
font-family: 'Questrial', sans-serif;
padding: 30 30px 0px 30px;
margin: 30px 30px 0px 30px;
hr {
    color: #D70170;
    background-color: #D70170;
    border-width: 0px;
    height: 2px;
}

@media (max-width: 700px) {
    padding: 0px;
    width: 80%;
}

`

const ContactLoc = styled.div`
text-align: center;

div {
    font-size: 18px;
    padding: 2px;
    display: flex;
    align-items: center;
    justify-content: center;    
}

img {
    height: 20px;
    margin-right: 2px;
}

@media (max-width: 700px) {
    div {
        font-size: 13px;
    }
}
`

const ContactSocials = styled.div`
display: flex;
align-items: center;
justify-content: center;
`

const ContactSection = ({isENG}) => {

    function sendEmail(e) {
        e.preventDefault();

        emailjs.sendForm('service_hu0gre8', 'template_vg1ocdd', e.target, 'user_vBv77OvQZ0xnE7SqbMDZf')
        .then((result) => {
            alert(result.text);
        }, (error) => {
            alert(error.text);
        });
        e.target.reset()
    }
    return (
        <StyledContact>
            <ContactTitle>
                <h2>{isENG ? "Contact" : "Kontakt"}</h2>
                <h4>{isENG ? "I'd love to hear from you" : "Czekam na Twoją wiadomość"}</h4>
            </ContactTitle>
            <ContactContent>
                
                <ContactInfo>
                    <ContactLoc>
                        <div style={{fontWeight: 600, color: "#FF859E"}}>Wojciech Ratajczak</div>
                        <div><img src={location} alt="location"/>Wroclaw, PL</div>
                        <div><img src={phone} alt="phone"/>+48509634294</div>
                        <div><img src={email} alt="email"/>wojciech.ratajczak92@gmail.com</div>
                    </ContactLoc>
                    <hr></hr>
                    <ContactSocials>
                        <Socialls/>
                    </ContactSocials>
                </ContactInfo>
                <ContactForm>
                    <form onSubmit={sendEmail}>
                    <div>
                        <input type="text" name="name" placeholder={isENG ? "Your name.." : "Twoje imię.."} required/>
                        <input type="email" name="email" placeholder={isENG ? "Your email.." : "Twój email.."} required />
                        <input type="text" name="subject" placeholder={isENG ? "Subject.." : "Temat.."} required/>
                        <textarea name="message" placeholder={isENG ? "Write something.." : "Napisz coś.."} required/>
                        <input type="submit" value={isENG ? "Send" : "Wyślij"} />
                    </div>
                </form>
                </ContactForm>
            </ContactContent>
        </StyledContact>
    )
}

export default ContactSection
