import React from 'react'
import styled from 'styled-components'
import {opacityAnimation} from '../animations'
import ProjectCard from './ProjectCard'
import {dataEng, dataPL, headerEng, headerPl } from './projectsData'
import { Swiper, SwiperSlide } from "swiper/react";
import "./styles/swiper.css";
import "./styles/pagination.css"
import SwiperCore, { Pagination} from 'swiper/core';

SwiperCore.use([ Pagination]);

const StyledProjects = styled.div`
background-color: #3D2A35;
color: #FFB7CB;
width: 100%;
height: fit-content;
animation: ${opacityAnimation} 1s ease-in-out;
padding-top: 40px;
padding-bottom: 15%;
display: flex;
flex-direction: column;
align-items: center;

@media (max-width: 1000px) {
    height: fit-content;
}
`

const ProjectsHeader = styled.div`
width: 100%;
text-align: center;

#p1 {
    color: #D70170;
    font-size: 1.5rem;
    font-family: 'Staatliches', cursive;
}
#p2 {
    padding: 15px 10px 30px 10px;
}

@media (max-width: 700px) {
  #p1 {
    font-size: 1.2rem;
  }
  #p2 {
    font-size: 0.9rem;
  }
}
`


const ProjectsContent = styled.div`
display: flex;
justify-content: center;
align-items: center;
width: 80%;
margin: 0 auto;
`

const Projects = ({isENG}) => {
    return (
       <StyledProjects>
           <ProjectsHeader>
                <div id="p1">{isENG ? "Projects" : "Projekty"}</div>
                <div id="p2">{isENG ? headerEng : headerPl }</div>
           </ProjectsHeader>
           <ProjectsContent>
           <Swiper pagination={{clickable: "true"}} className="mySwiper" slidesPerView={1} spaceBetween={10} breakpoints={{
  "700": {
    "slidesPerView": 1,
    "centeredSlides": true
  },
  "768": {
    "slidesPerView": 2,
  },
  "1024": {
    "slidesPerView": 3,
  },
  "1400": {
    "slidesPerView": 4,
  }
}}>
                    {isENG ? 
                        dataEng.map(d => 
                        <SwiperSlide key={d.id}><ProjectCard isENG={isENG} image={d.imageURL} description={d.text} title={d.title} techs={d.techs} cardlink={d.cardlink} > 
                        </ProjectCard></SwiperSlide>
                        )
                     : 
                        dataPL.map(d => 
                            <SwiperSlide key={d.id}>     
                        <ProjectCard isENG={isENG} key={d.id} image={d.imageURL} description={d.text} title={d.title} techs={d.techs} cardlink={d.cardlink} > 
                        </ProjectCard></SwiperSlide>
                        )
                    }
                    </Swiper>
           </ProjectsContent>
       </StyledProjects>
    )
}

export default Projects
